<template>
    <div>
        <common-header :h_title="h_title" :showback="true" :bgcolor='head_color'></common-header>
        <div v-if="showFansList">
            <!-- <div class="f_fans" style='height:{{height}}px'> -->
            <div class="f_fans">
                <return-top @returnTop='goTop'></return-top>
                <div id='f_fans' style='height:0.9rem'></div>
                    <div class='f_fans_fixed'>
                        <div class='f_fansNum'>
                            <span>{{fansNum}}</span>
                            <span>总粉丝数</span>
                        </div>
                        <div class='f_new_fansNum'>
                            <span>今日新增粉丝： {{newFans}}人</span>
                            <span v-if="isfouvip!=0">今日下单数： {{todayOrders}}单</span>
                        </div>
                        <div class='detailed_nav'>
                            <div @click="fanType('1')" :class="detailType=='1'?'span_active':'span1'" id='1'>普通用户</div>
                            <div @click="fanType('2')" :class="detailType=='2'?'span_active':'span1'" id='2'>会员</div>
                        </div>
                        <div class='f_fans_nav'>
                            <div :class="fansType=='1'?'span_active':'span'" id='1'>
                                <div @click='fansAll' >
                                    <span v-if="days=='all'" id='all' >全部</span>
                                    <span v-if="days=='today'" id='today'>今日</span>
                                    <span v-if="days=='seven'" id='seven'>近7天</span>
                                    <span v-if="days=='thirty'" id='thirty'>近30天</span>
                                    <i :class="dowmListinfo ? 'iconfont icon-asc':'iconfont icon-desc'"></i>
                                </div>
                                <div class='dowmList' v-if="dowmListinfo">
                                    <div v-for="(item,index) in dateList" :key="index" :class=" days == item.id ? 'active' : ''" @click='choseList(item.id)' id='item.id'>
                                        {{item.name}}
                                    </div>
                                </div>
                            </div>
                            <div @click='activeAll' :class="is_active=='1'?'span_active':'span'" id='2' v-if="isfouvip!=0">
                                <span>活跃</span>
                                <i class="iconfont icon-shaixuan1"></i>
                            </div>
                        </div>
                    </div>
                    <div v-infinite-scroll="loadMore" infinite-scroll-disabled="is_load" infinite-scroll-distance="30" class="scroll">
                        <div class='head_top'></div>
                        <div class='f_fans_List'>
                            <div class='list_info' v-for="(listblock,index) in fansList"  :key="index">
                              <div class='list_info_wraper' v-for="(item,index) in listblock" :key="index">
                                  <div class='list_info_timg'>
                                      <img class='list_info_timg_ava' :src='item.image'/>
                                      <img class='list_info_timg_vip' :src="imgPath+'shop/vip1.png'" v-if="detailType=='2'"/>
                                  </div>
                                  <div class='list_info_right'>
                                      <div class='list_info_right_l'>
                                          <div style="display:flex">
                                              <span class="span_overflow">{{item.nickname}}</span>
                                              <span v-if="isfouvip!=0">贡献佣金{{item.commission}}元</span>
                                          </div>
                                         <span class='text' v-if="fansType==1">注册时间：{{item.createtime}}</span>
                                          <span class='text' v-if="fansType==2&&isfouvip!=0">活跃时间：{{item.last_active_time}}</span>
                                      </div>
                                      <div class='list_info_right_r' v-if="isfouvip!=0">
                                        <span>{{item.last_active_time}}</span>
                                        <span>累计下单{{item.total_order}}单</span>
                                      </div>
                                  </div>
                              </div>
                            </div>
                        </div>
                        <div class="mar_bottom">
                            <div class='more_load' v-if='!is_load'>
                                <mt-spinner type="fading-circle"></mt-spinner>
                            </div>
                            <div class='s_fans_bottom' v-if='!hasData'>
                                <img :src="imgPath+'pro/null.png'" mode='widthFix'/>
                            </div>
                            <div class='order-foot'  v-if="hasData&&is_end">
                                <span>—————— 我是有底线的 ——————</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="invitation" @click="goInvitation">
                    <mt-button  hover-class="other-button-hover">继续邀请</mt-button>
                </div>
            </div>
        </div>
</template>
<script>
import commonHeader from 'common/common-header'
import returnTop from '@/components/return-top'
import {
  Indicator,
  InfiniteScroll,
  Button
} from 'mint-ui'
import {
  fansList
} from '@/api/share'
export default {
  name: 'FansList',
  components: {
    commonHeader,
    InfiniteScroll,
    Indicator,
    Button,
    returnTop
  },
  data() {
    return {
      imgPath: process.env.FilE_URL,
      h_title: '粉丝列表',
      head_color: {
        background: '#f3c0a2',
        color: '#000'
      },
      showFansList: false,
      fansType: 1,
      is_active: 0,
      days: 'all',
      isfouvip: 0,
      //   is_iphonex: app.globalData.phoneType,
      fansNum: 0,
      todayOrders: 0,
      newFans: 0,
      fansList: [],
      height: '',
      topHeight: '',
      allowScorll: true,
      is_load: false,
      hasData: true,
      is_end: false,
      total: 0,
      topNum: 0,
      dateList: [{ id: 'all', name: '全部' }, { id: 'today', name: '今日' }, { id: 'seven', name: '近7天' }, { id: 'thirty', name: '近30天' }],
      dowmListinfo: false,
      dowmdate: 1,
      detailType: '1',
      page: 1
    }
  },
  mounted() {
    Indicator.open('加载中...')
    this.getList()
  },
  methods: {
    // 回到顶部
    goTop() {
      console.log('回到顶部')
      document.getElementById('f_fans').scrollIntoView({
        block: 'start',
        behavior: 'smooth'
      })
    },
    // 普通会员、会员 激活状态
    fanType(id) {
    //   console.log(e, '111')
      this.detailType = id
      this.page = 1
      this.days = 'all'
      this.dateType = '1'
      this.fansList = []
      this.is_active = 0
      this.hasData = true
      this.is_end = false
      this.is_load = false
      this.getList()
    },
    // 请求列表数据
    getList() {
      var that = this
      Indicator.open('加载中...')
      let obj = {
        type: that.detailType,
        page: that.page,
        days: that.days,
        is_active: that.is_active
      }
      fansList(obj).then(res => {
        if (res.isSuccess === true || res.isSuccess === 'true') {
          Indicator.close()
          if (res.data.fans_list.rows.length > 0) {
            let currentpage = that.page - 1
            if (currentpage === 0) {
              that.fansList = []
            }
            that.showFansList = true
            // fansList: res.data.fans_list.rows,
            that.fansList[currentpage] = res.data.fans_list.rows
            that.newFans = res.data.today_fans
            that.fansNum = res.data.total_fans
            that.total = res.data.fans_list.total
            that.todayOrders = res.data.today_orders
            that.isfouvip = that.$store.state.home.userInfo.level_id
            that.hasData = true
            that.is_end = false
            that.is_load = false
            // 如果一页加载数据等于总条数
            if (res.data.fans_list.rows.length === Number(res.data.fans_list.total)) {
              that.is_load = true
              that.hasData = true
              that.is_end = true
            }
          } else {
            that.showFansList = true
            that.newFans = res.data.today_fans
            that.todayOrders = res.data.today_orders
            that.fansNum = res.data.total_fans
            that.total = res.data.fans_list.total
            that.isfouvip = that.$store.state.home.userInfo.level_id
            that.hasData = false
            that.is_load = true
            that.is_end = true
            console.log(!that.hasData, '111')
          }
        } else {
          Indicator.close()
          console.log('数据请求失败')
        }
      })
    },
    loadMore() {
      var that = this
      this.is_load = true
      //   if (this.is_load) {
      console.log(this.is_load)
      // if (this.is_load === false) {
      let obj = {
        type: that.detailType,
        page: ++that.page,
        days: that.days,
        is_active: that.is_active
      }
      Indicator.open('加载中...')
      fansList(obj).then(res => {
        if (res.isSuccess === true) {
          Indicator.close()
          if (res.data.fans_list.rows.length > 0) {
            let currentpage = that.page - 1
            that.fansList[ currentpage ] = res.data.fans_list.rows
            console.log(that.fansList, 'loadMore')
            that.is_end = false
            that.is_load = false
            if (JSON.stringify(res.data.fans_list.rows) === '[]' || that.fansList[currentpage].length < 10) {
              that.cashMoney = res.data.wind
              that.is_load = true
              that.is_end = true
            }
            console.log(that.redList)
          } else {
            that.cashMoney = res.data.wind
            that.is_end = true
            that.is_load = true
          }
        }
      })
    },
    // 邀请跳转
    goInvitation() {
      this.$router.togo({
        path: '/poster'
      })
    },
    // 点击出现下拉框，现则值
    fansAll(e) {
      this.dowmListinfo = !this.dowmListinfo
      this.fansType = 1
      this.is_active = 0
    },
    // 激活状态情况
    activeAll() {
      if (this.is_active === 0 || this.is_active === '0') {
        this.dowmListinfo = false
        this.is_active = 1
        this.fansType = 2
        this.days = 'all'
        this.hasData = false
        this.is_end = true
        this.is_load = false
        this.fansList = []
      } else {
        this.dowmListinfo = false
        this.is_active = 0
        this.fansType = 2
        this.days = 'all'
        this.hasData = false
        this.is_end = true
        this.is_load = false
        this.fansList = []
      }
      this.getList()
    },
    // 关闭下拉框
    choseList(id) {
      this.days = id
      this.dowmListinfo = false
      this.fansList = []
      this.page = 1
      this.hasData = false
      this.is_end = true
      this.is_load = false
      this.getList()
    }
  }
}
</script>
<style lang="less" scoped>
@import '~styles/index.less';
@import '~styles/variable.less';
.f_fans {
  position: fixed;
  top: 0;
  bottom: 0;
  background: #fff;
  width: 100%;
  height: 100%;
  overflow: scroll;
  margin-bottom: 110/100rem;
}
.scroll{
     position: relative;
  top: 0;
  background: #fff;
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.f_fans_bottom {
  margin-bottom: 178/100rem;
}

.f_fansNum {
  height: 160/100rem;
  background: #f3c0a2;
  /* border-top: 1px solid #ececec; */
  text-align: center;
  font-size: 80/100rem;
  color: #fff5ce;
  padding-top: 30/100rem;
}

.f_fansNum span:nth-child(2) {
  display: block;
  font-size: 33/100rem;
  margin-top: -10/100rem;
  text-align: center;
  width: 100%;
}

.f_new_fansNum {
  border-bottom: 1px solid #ececec;
  height: 72/100rem;
  line-height: 72/100rem;
  background: #f5c4a7;
  padding: 0 30/100rem;
  color: #fff;
  font-size: 26/100rem;
  clear: both;
}

.f_new_fansNum span:nth-child(1) {
  float: left;
}

.f_new_fansNum span:nth-child(2) {
  float: right;
}

.f_fans_nav {
  background: #f6f6f6;
  border-bottom: 1px solid #ececec;
  height: 90/100rem;
  line-height: 90/100rem;
  text-align: center;
  display: flex;
  justify-content: space-between;
}

.f_fans_nav .span {
  position: relative;
  height: 87/100rem;
  line-height: 87/100rem;
  width: 200/100rem;
  text-align: center;
  font-size: 28/100rem;
}
.f_fans_nav .span div{
    height: 90/100rem;
  line-height: 90/100rem;
  text-align: center;
}
.f_fans_nav .dowmList {
  position: absolute;
  width: 200/100rem;
  text-align: center;
  color: #353535;
  z-index: 2;
  background: rgba(252, 252, 252, 0.9);
}

.f_fans_nav .dowmList .active {
  background: #f2f2f2;
}

.f_fans_nav .span_active {
  width: 200/100rem;
    height: 90/100rem;
  line-height: 90/100rem;
  text-align: center;
  color: #f3c0a2;
  /* border-bottom: 3px solid #f3c0a2; */
  font-size: 28/100rem;
}
.f_fans_nav .span_active div{
height: 90/100rem;
  line-height: 90/100rem;
  text-align: center;
}
.f_fans_nav .span_active i {
  color: #f3c0a2;
}

.f_fans_nav i {
  color: #999;
}

.f_fans_List {
  margin-bottom: 15/100rem;
}
.f_fans_List .list_info{

}
.f_fans_List .list_info .list_info_wraper {
  clear: both;
  height: 108/100rem;
  padding: 15/100rem 30/100rem 8/100rem;
}

.f_fans_List .list_info .list_info_timg {
  float: left;
  width: 80/100rem;
  height: 80/100rem;
  border-radius: 50%;
  margin-top: 5/100rem;
  position: relative
}

.f_fans_List .list_info .list_info_timg .list_info_timg_ava {
  position: relative;
  width: 80/100rem;
  height: 80/100rem;
  border-radius: 50%;
}
.f_fans_List .list_info .list_info_timg .list_info_timg_vip {
  position: absolute;
  right:5/100rem;
  top: -8/100rem;
  width: 30/100rem;
  height: 30/100rem;
  border-radius: 50%;
  transform: rotate(15deg)
}

.f_fans_List .list_info_right {
  float: right;
  width: 570/100rem;
  border-bottom: 1px solid #dfdfdf;
  font-size: 34/100rem;
  color: #353535;
  padding-top: 10/100rem;
  margin-left: 20/100rem;
}

.f_fans_List .list_info_right .list_info_right_l {
  float: left;
  font-size: 28/100rem;
}

.f_fans_List .list_info_right .list_info_right_l .span_overflow {
  max-width: 150/100rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
}

.f_fans_List .list_info_right .list_info_right_l div span:nth-child(2) {
  display: inline-block;
  padding-left: 10/100rem;
  width: 2.7rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.list_info_right_l span.text {
  display: block;
  font-size: 24/100rem;
  color: #999;
  line-height: 66/100rem;
}

.f_fans_List .list_info_right .list_info_right_r {
  float: right;
  text-align: right;
  font-size: 24/100rem;
  padding-top: 6/100rem;
}

.list_info_right_r span:nth-child(2) {
  display: block;
  font-size: 24/100rem;
  color: #999;
  line-height: 70/100rem;
}

.order-foot {
  height:84/100rem;
  line-height:84/100rem;
  text-align: center;
  font-size: 24/100rem;
  color: #bebebe;
  margin-bottom: 10/100rem;
}

.order-foot span {
  display: inline-block;
  font-size: 23.97/100rem;
  color: #bebebe;
}
.s_fans_bottom {
  text-align: center;
  font-size: 30/100rem;
  /* font-weight: bold; */
  padding-top:100/100rem;
}
.s_fans_bottom img{
  display: inline-block;
  width: 246/100rem;
  height: 368/100rem;
}
.s_fans_list_null {
  text-align: center;
  font-size: 30/100rem;
  /* font-weight: bold; */
  padding-top: 100/100rem;
}

.s_fans_list_null img {
  width: 246/100rem;
  height: 368/100rem;
}
.invitation {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100/100rem;
  background: #fff;
}

.invitation .mint-button {
  line-height: 100/100rem;
  height: 1rem;
  width: 100%;
  color: #fff;
  background: #f3c0a2;
  font-size: 40/100rem;
  border-radius: 0;
}

.invitation_bottom {
  padding-bottom: 68/100rem;
}

.scroll {
  overflow: scroll;
}

/* .scroll_bottom{
  margin-bottom: 88/100rem;
} */

.f_fans_fixed {
  position: fixed;
  top: 0.9rem;
  width: 100%;
  z-index: 2;
}

.head_top {
  height: 446/100rem;
  background: transparent;
  z-index: 10;
}

.mar_bottom{
  margin-bottom: 20/100rem;
//   height:200/100rem;
}
/deep/.more_load{
   border:none;
   background:#ffffff;
//    font-size:20/100rem;
   text-align: center;
   margin-top: 0.05rem
}
/*tab切换添加*/
.detailed_nav {
  background: #fff;
  position: relative;
  height:89/100rem;
  line-height:89/100rem;
  width: 100%;
  /* clear: both; */
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid #ececec;

}
.detailed_nav div {
  color: #666;
  font-size: 30/100rem;
  text-align: center;
  height: 87/100rem;
  line-height: 87/100rem;
  width: 50%;
}

.detailed_nav .span_active {
  border-bottom: 4/100rem solid #f5c4a7;
  color: #f5c4a7;
  text-align: center;
}
</style>



